import {
  Text,
  StyleSheet,
  View,
  ActivityIndicator,
  TextInput,
  Platform,
  TouchableOpacity,
  ScrollView,
} from 'react-native';
import React, { Component } from 'react';
import * as Fonts from 'expo-font';
import { ref, getDatabase, set, update, onValue } from '@firebase/database';
import db from '../config';

const fonts = {
  font1: require('../assets/fonts/font1/Edu_TAS_Beginner/static/EduTASBeginner-Regular.ttf'),
  font2: require('../assets/fonts/font2/Roboto_Mono/static/RobotoMono-Regular.ttf'),
  font3: require('../assets/fonts/font3/M_PLUS_Code_Latin/static/MPLUSCodeLatin/MPLUSCodeLatin-Regular.ttf'),
  font4: require('../assets/fonts/font4/MuseoModerno/static/MuseoModerno-Regular.ttf'),
};

export default class Home extends Component {
  constructor() {
    super();
    this.state = {
      fontsLoaded: false,
      1: '',
      2: '',
      3: '',
      4: '',
      filled: false,
      actualPin: 0,
      stt: '',
    };
  }

  btnPress = () => {
    if (this.state[1] && this.state[2] && this.state[3] && this.state[4]) {
      var enteredPin = `${this.state[1]}${this.state[2]}${this.state[3]}${this.state[4]}`;

      if (parseInt(enteredPin) === this.state.actualPin) {
        var refff = ref(getDatabase(), '/');
        if (this.state.stt === 'open') {
          update(refff, { state: 'close' });
        } else if (this.state.stt === 'close') {
          update(refff, { state: 'open' });
        }
      }

      console.log(parseInt(enteredPin));
    } else {
      alert('Enter pin !');
    }
  };

  fetchPin = () => {
    var reff = ref(getDatabase(), '/');
    onValue(reff, (snapshot) => {
      this.setState({
        actualPin: snapshot.val().pin,
        stt: snapshot.val().state,
      });
      console.log(this.state.actualPin + ' ' + snapshot.val().state);
    });
  };

  componentDidMount() {
    Fonts.loadAsync(fonts).then(() => {
      this.setState({ fontsLoaded: true });
    });
    this.fetchPin();
  }

  componentDidUpdate() {
    this.refs.inputText1.focus();

    if (this.state[1].length == 1) {
      this.refs.inputText2.focus();
    }

    if (this.state[2].length == 1) {
      this.refs.inputText3.focus();
    }

    if (this.state[3].length == 1) {
      this.refs.inputText4.focus();
    }
    if (this.state[4].length == 1) {
      this.btnPress();
      this.refs.inputText1.clear();
      this.refs.inputText2.clear();
      this.refs.inputText3.clear();
      this.refs.inputText4.clear();
      this.refs.inputText1.focus();
      this.setState({
        1: '',
        2: '',
        3: '',
        4: '',
      });
      console.log('Filled');
    }
  }
  render() {
    if (this.state.fontsLoaded) {
      return (
        <View style={styles.container}>
          <ScrollView>
            <View style={styles.secondContainer}>
              <Text style={styles.text}>Remote controlled lock</Text>
              <View style={{ flexDirection: 'row' }}>
                <TextInput
                  style={
                    Platform.OS === 'android' ? styles.input : styles.input2
                  }
                  placeholder={Platform.OS === 'web' ? 'Enter Pin' : ''}
                  onChangeText={(text) => {
                    this.setState({ 1: text });
                  }}
                  maxLength={1}
                  ref="inputText1"
                />
                <TextInput
                  style={
                    Platform.OS === 'android' ? styles.input2 : styles.input2
                  }
                  placeholder={Platform.OS === 'web' ? 'Enter Pin' : ''}
                  onChangeText={(text) => {
                    this.setState({ 2: text });
                  }}
                  maxLength={1}
                  ref="inputText2"
                />
                <TextInput
                  style={
                    Platform.OS === 'android' ? styles.input2 : styles.input2
                  }
                  placeholder={Platform.OS === 'web' ? 'Enter Pin' : ''}
                  onChangeText={(text) => {
                    this.setState({ 3: text });
                  }}
                  maxLength={1}
                  ref="inputText3"
                />
                <TextInput
                  style={
                    Platform.OS === 'android' ? styles.input2 : styles.input2
                  }
                  placeholder={Platform.OS === 'web' ? 'Enter Pin' : ''}
                  onChangeText={(text) => {
                    this.setState({ 4: text });
                  }}
                  maxLength={1}
                  ref="inputText4"
                />
              </View>
              <View style={{}}>
                <TouchableOpacity
                  style={{
                    margin: 30,
                    backgroundColor: '#56e2b5',
                    padding: 15,
                    width: 150,
                    alignItems: 'center',
                    borderRadius: 10,
                    alignSelf: 'center',
                  }}
                  onPress={() => {
                    this.refs.inputText1.clear();
                    this.refs.inputText2.clear();
                    this.refs.inputText3.clear();
                    this.refs.inputText4.clear();
                    this.refs.inputText1.focus();

                    this.setState({
                      1: '',
                      2: '',
                      3: '',
                      4: '',
                      filled: false,
                    });
                  }}
                >
                  <Text
                    style={{
                      fontFamily: 'font4',
                      fontSize: 24,
                    }}
                  >
                    Clear
                  </Text>
                </TouchableOpacity>
                <TouchableOpacity
                  style={{
                    margin: 30,
                    backgroundColor: '#96db75',
                    padding: 15,
                    minWidth: 250,
                    alignItems: 'center',
                    borderRadius: 10,
                    marginTop: 0,
                  }}
                  onPress={this.btnPress}
                >
                  <Text
                    style={{
                      fontFamily: 'font4',
                      fontSize: 24,
                    }}
                  >
                    Submit
                  </Text>
                </TouchableOpacity>
                <Text
                  style={{
                    fontFamily: 'font1',
                    fontSize: 24,
                  }}
                >
                  Status : {this.state.stt}
                </Text>
              </View>
            </View>
          </ScrollView>
        </View>
      );
    } else {
      return (
        <View
          style={{
            height: '100%',
            backgroundColor: 'white',
            alignItems: 'center',
            flex: 1,
            justifyContent: 'center',
          }}
        >
          <ActivityIndicator size={80} color="#36a86d" />
        </View>
      );
    }
  }
}

const styles = StyleSheet.create({
  container: {
    height: '100%',
    backgroundColor: 'white',
  },
  text: {
    fontFamily: 'font2',
    fontSize: 35,
    textDecorationLine: 'underline',
    textDecorationColor: 'black',
    textAlign: 'center',
    marginBottom: 30,
  },
  secondContainer: {
    alignItems: 'center',
    margin: 30,
    alignSelf: 'center',
  },

  input: {
    marginHorizontal: 10,
    padding: 15,
    borderColor: 'black',
    borderWidth: 1,
    borderRadius: 15,
    flex: 1,
    fontFamily: 'font2',
    color: '#5656e2',
    textAlign: 'center',
    width: '50%',
  },
  input2: {
    marginHorizontal: 10,
    padding: 15,
    borderColor: 'black',
    borderWidth: 1,
    borderRadius: 15,
    flex: 1,
    fontFamily: 'font2',
    color: '#5656e2',
    textAlign: 'center',
  },
});
